.titleBox{
    display: inline-block;
    padding: 10px 0px 10px 0px;
    margin: 5px 0px 5px 0px;
    width: 100%;
    text-align: center;
    border-bottom: 1px solid red;
}


  .Title {
    padding-top: 10px;
  }
  
  .modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
  }
  
  .modal-main {
    position: fixed;
    background: white;
    width: 80%;
    height: auto;
    top: 50%;
    left: 50%;
    border: solid #007ea7;
    border-radius: 5px;
    background-color: #007ea7;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  
  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
  }
  
  .input-username {
    display: block;
    margin: 10px auto 5px auto;
    padding: 2px 5px 3px 5px;
    border-radius: 3px;
    border: solid #eeeeee;
    background-color: #eeeeee;
  }
  .input-password {
    display: block;
    margin: 5px auto 5px auto;
    padding: 2px 5px 3px 5px;
    border-radius: 3px;
    border: solid #eeeeee;
    background-color: #eeeeee;
  }
  
  .modal-submit {
    width: 20%;
    height: 25px;
    border-radius: 3px;
    border: solid #ff7700;
    background-color: #ff7700;
  }
  
  /*
  for close button
  */
  .close {
    color: white;
    float: right;
    font-size: 32px;
  }
  
  .close:hover,
  :focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
  }

  .close {
    position: fixed;
    margin-top: -1.5%;
    margin-left: 40.9%;
    opacity: 0.8;
  }
  .close:hover {
    opacity: 1;
  }
  .close:before,
  .close:after {
    position: absolute;
    left: 20px;
    content: "";
    height: 40px;
    width: 40px;
  }
  

